/* Reset básico */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  /* Estilo para el body */
  body {
    font-family: 'Arial', sans-serif;
    line-height: 1.6;
    background-color: #f4f4f4;
    color: #333;
    margin: 0;
    padding: 0;
  }
  
  /* Contenedor principal */
  .App {
    text-align: center;
    padding: 20px;
  }
  
  /* Estilo para encabezados */
  h1, h2, h3, h4, h5, h6 {
    margin-bottom: 20px;
    color: #444;
  }
  
  /* Links */
  a {
    text-decoration: none;
    color: #007BFF;
  }
  
  a:hover {
    color: #0056b3;
    text-decoration: underline;
  }
  
  /* Botones */
  button {
    padding: 10px 20px;
    background-color: #007BFF;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  
  /* Contenedor de tarjetas (por ejemplo, para Pokémon) */
  .pokemon-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 20px;
    padding: 20px;
  }
  
  /* Tarjetas individuales */
  .pokemon-card {
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 10px;
    padding: 15px;
    text-align: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s ease, box-shadow 0.2s ease;
  }
  
  .pokemon-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2);
  }
  
  /* Imágenes dentro de tarjetas */
  .pokemon-card img {
    width: 100px;
    height: 100px;
    margin-bottom: 10px;
  }
  
  /* Footer */
  footer {
    margin-top: 20px;
    padding: 10px 0;
    background-color: #007BFF;
    color: #fff;
    text-align: center;
  }
  